import React from "react";
import {
  SafeAreaView,
  StyleSheet,
  View,
  ScrollView,
  Image,
} from "react-native";

//<!-- libs-->
import {
  Divider,
  Icon,
  Layout,
  Text,
  TopNavigation,
  TopNavigationAction,
} from "@ui-kitten/components";
import Markdown from "react-native-easy-markdown";

//<!--custom-->
import { ticksToDate } from "./../utils/dateUtils";

//<!--COMPONENT-->
const BackIcon = (props) => <Icon {...props} name="arrow-back" />;

//<!--JOURNAL-DETAIL-->
const JournalDetail = ({ navigation, route }) => {
  const navigateBack = () => {
    navigation.goBack();
  };

  const BackAction = () => (
    <TopNavigationAction icon={BackIcon} onPress={navigateBack} />
  );

  const journal = route.params.journal;
  let ticks = Number(journal.entrycreateddate);
  let dt = ticksToDate(ticks);

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <TopNavigation
        alignment="center"
        accessoryLeft={BackAction}
        title={`${dt.utcMonth} ${dt.utcDate}, ${dt.utcYear}`}
      />
      <Divider />
      <Layout style={{ flex: 1 }}>
        <ScrollView style={styles.scrollView}>
          {journal.imagedata ? (
            <Image
              style={{
                width: "100%",
                // aspectRatio: 1,
                resizeMode: "cover",
                height: 400,
              }}
              source={{
                uri: `data:image/png;base64,${journal.imagedata}`,
              }}
            />
          ) : null}

          <Layout
            style={{
              paddingHorizontal: 20,
            }}
          >
            {journal.title ? (
              <View>
                {/* title */}
                <Text style={styles.title} category="h4">
                  {journal.title}
                </Text>
              </View>
            ) : null}

            {/* date */}
            <Text
              category="p1"
              appearance="hint"
              style={{ paddingVertical: 6 }}
            >
              {`${dt.utcMonth} ${dt.utcDate}, ${dt.utcYear}`}
            </Text>

            <Markdown useDefaultStyles markdownStyles={styles}>
              {journal.richcontent}
            </Markdown>
          </Layout>
        </ScrollView>
      </Layout>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollView: {},
  title: {
    paddingTop: 20,
    paddingBottom: 0,
    //borderWidth: 1,
  },
  text: {
    color: "#505050",
    fontSize: 17,
    textAlign: "left",
    lineHeight: 17 * 1.5,
    //letterSpacing: 0.5,
    paddingBottom: 20,
    paddingTop: 12,
  },

  h2: {
    fontSize: 24,
    marginTop: 20,
    marginBottom: 8,
  },
});

export default JournalDetail;
