import _ from "lodash";
import { Dropbox } from "dropbox";
import { Buffer } from "buffer";
const schema = require("../../utils/schema");

import { GET_DEFAULT_DATA, POST_DEFAULT_DATA, LOGOUT } from "./types";
import {
  SET_ACCESS_TOKEN,
  SET_ACCOUNT,
  SET_METADATA,
  GET_DATA,
  CREATE_DATA,
  UPDATE_DATA,
  DOWNLOAD_FILE,
  UPLOAD_FILE,
  DELETE_FILE,
  ERROR_GET_METADATA,
  ERROR_GET_DATA,
  ERROR_DOWNLOAD_FILE,
  ERROR_UPLOAD_FILE,
  ERROR_DELETE_FILE,
} from "./types";

var dbx = new Dropbox({
  fetch: fetch,
});

export const setAccessToken = (result) => async (dispatch) => {
  dbx.accessToken = result.access_token;

  dispatch({
    type: SET_ACCESS_TOKEN,
    payload: result.access_token,
  });

  const account = await dbx.usersGetAccount({
    account_id: result.account_id,
  });

  dispatch({
    type: SET_ACCOUNT,
    payload: account,
  });
};

export const getFileMetadata = async (path) => {
  try {
    return await dbx.filesDownload({
      path: `${path}`,
    });
  } catch (error) {
    console.log("Error downloading the file ❎");
  }
};

export const getJournals = () => async (dispatch, getState) => {
  try {
    dbx.accessToken = getState().dataStore.accessToken;
    console.log(`~~~~~~~~~~~~~~~~~~~~~~~~~~~~~-~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~`);
    console.log(`<--------------BEGIN-GETSTORE----------------`);
    const dataStore = getState().dataStore;

    console.log(
      JSON.stringify(
        _.isEmpty(dataStore.metadata)
          ? `NO METADATA`
          : `METADA.ENTRIES::${dataStore.metadata.entries.length}`
      )
    );
    console.log(`--------------END-GETSTORE---------------->`);

    let cursor;
    let metadata;

    if (_.isEmpty(metadata)) {
      //get server metadata
      console.log(`getting server metadata...`);
      metadata = await dbx.filesListFolder({
        path: "",
        limit: 5,
      });
    } else {
      cursor = dataStore.metadata.cursor;
      console.log(`cursor:${cursor}`);

      //get server metadata with cursor
      metadata = await dbx.filesListFolderContinue({
        cursor,
      });
    }

    console.log(`Metadata entries retrieved..${metadata.entries.length}`);

    //sort descending by server_modified date
    metadata.entries.sort(
      (a, b) => Date.parse(b.server_modified) - Date.parse(a.server_modified)
    );

    //get only file entries
    metadata.entries = metadata.entries.filter((item) => {
      return (item[".tag"] === "file") & item.name.endsWith(".entry");
    });

    //compare server-entries with client-entries
    let entriesToDownload = [];
    if (!_.isEmpty(dataStore.metadata)) {
      console.log(`<--------------START-COMPARE----------------`);

      //get client-entries
      let dataStoreEntries = dataStore.metadata.entries;

      let entryFound = false;
      for (let i = 0; i < metadata.entries.length; i++) {
        //resets entryFound on each iteration
        entryFound = false;

        //server-entry
        const entry = metadata.entries[i];

        //loop through client-entries & compare id then timestamp
        for (let j = 0; j < dataStoreEntries.length; j++) {
          //client-entry
          const dataStoreEntry = dataStoreEntries[j];

          if (entry.id === dataStoreEntry.id) {
            //mark entryFound to true
            entryFound = true;

            const millis =
              Date.parse(entry.server_modified) -
              Date.parse(dataStoreEntry.server_modified);

            if (millis > 0) {
              console.log(
                `Adding to entriesToDownload as timestamp difference=${millis}!!`
              );
              entriesToDownload = [...entriesToDownload, entry];
            }

            break;
          }
        }
        if (!entryFound) {
          entriesToDownload = [...entriesToDownload, entry];
        }
      }
      console.log(`--------------END-COMPARE---------------->`);
    } else {
      entriesToDownload = metadata.entries;
    }

    dispatch({
      type: SET_METADATA,
      payload: metadata,
    });

    //loop through entries to download
    for (let entry of entriesToDownload) {
      //download content
      console.log(`downloading..${entry.name}`);
      let meta = await getFileMetadata(entry.path_lower);

      //read content
      let reader = new FileReader();
      reader.onloadend = (event) => {
        let result = reader.result.split(",", 2)[1];
        let buff = new Buffer(result, "base64");
        const entryData = schema.Entry.deserializeBinary(new Uint8Array(buff));

        //print to the console: entry JSON
        // console.log(`--------------BEGIN----------------`);
        // console.log(entryData.toObject());
        // console.log(`--------------END----------------`);

        dispatch({
          type: GET_DATA,
          payload: entryData.toObject(),
        });
      };
      reader.readAsDataURL(meta.fileBlob);
    }
  } catch (error) {
    console.log(`error=>${JSON.stringify(error.error)}`);
    return {
      type: ERROR_GET_METADATA,
    };
  }
};

