import React, { useAuthRequest, useState, useEffect } from "react";
import { StyleSheet, Platform, View } from "react-native";

//<!--expo-->
import { Ionicons } from "@expo/vector-icons";
import * as AuthSession from "expo-auth-session";
import * as WebBrowser from "expo-web-browser";

//<!--redux-->
import Constants from "expo-constants";
import { connect } from "react-redux";

//<!--libs-->
import { Layout, Button, Text, Icon } from "@ui-kitten/components";

//<!--custom-->
import { parseQueryString, getUrlParams } from "./../utils/webUtils";
import { setAccessToken } from "../redux/actions/dataAction";
import AppIcon from "./../components/AppIcon";
import { Dropbox } from "dropbox";

if (Platform.OS === "web") {
  Constants.manifest.id = `@aneesahammed/${Constants.manifest.slug}`;
  WebBrowser.maybeCompleteAuthSession();
}

//<!--WELCOME-->
const Welcome = ({ navigation, setAccessToken }) => {
  useEffect(() => {}, []);

  const DropboxIcon = (props) => (
    <Ionicons
      name="logo-dropbox"
      size={32}
      color="#383838"
      style={{ marginRight: 20 }}
    />
  );

  const _handleLogin = async () => {
    let clientId = "5jtw1f8hfieyk8g";
    let redirectURL = "";
    let authUrl = ``;

    //let dbx = new Dropbox({ fetch: fetch, clientId });
    //authUrl = dbx.getAuthenticationUrl("http://localhost:8080/auth");

    if (Platform.OS === "web") {
      console.log(`[WEB AUTH]]`);
      if (__DEV__) {
        console.log(`DEV-MODE`);
        redirectURL = `https://localhost:19006/redirect`;
      } else {
        console.log(`PROD--MODE`);
        redirectURL = `https://support.getglimpses.com/glimpses-web/redirect`;
      }

      authUrl = `https://www.dropbox.com/1/oauth2/authorize?force_reapprove=true&response_type=token&redirect_uri=${redirectURL}&client_id=${clientId}`;

      const webResult = await WebBrowser.openAuthSessionAsync(
        authUrl,
        redirectURL
      );
      console.log(`WEB AUTH::${JSON.stringify(webResult)}`);

      if (webResult.type == "success") {
        let result = getUrlParams(webResult.url);

        result = {
          access_token: result[`${redirectURL}#access_token`],
          account_id: result.account_id,
        };
        console.log(JSON.stringify(result));

        setAccessToken(result);
      }
    } else {
      console.log(`[DEVICE AUTH]]`);
      redirectURL = AuthSession.getRedirectUrl();
      authUrl = `https://www.dropbox.com/1/oauth2/authorize?force_reapprove=true&response_type=token&redirect_uri=${redirectURL}&client_id=${clientId}`;

      let deviceResult = await AuthSession.startAsync({ authUrl });
      console.log(`RESULT OF DEVICE AUTH::${JSON.stringify(deviceResult)}`);

      if (deviceResult.type === "success") {
        setAccessToken(deviceResult.params);
      }
    }
  };

  return (
    <Layout style={{ flex: 1, alignItems: "center", justifyContent: "center" }}>
      <AppIcon height="100" width="100" />
      <Text category="h1">glimpses</Text>
      <Text
        appearance="hint"
        style={{
          textAlign: "center",
          fontWeight: "300",
          fontSize: 17,
          paddingVertical: 12,
        }}
      >
        {" "}
        Capture your memories {"\n"}to remember life..{" "}
      </Text>
      <Button
        style={styles.button}
        status="primary"
        accessoryLeft={DropboxIcon}
        onPress={_handleLogin}
      >
        Log in with Dropbox
      </Button>
    </Layout>
  );
};

const styles = StyleSheet.create({
  button: {
    margin: 2,
    justifyContent: "center",
    width: 300,
  },
});

export default connect(null, { setAccessToken })(Welcome);
